// frontend/src/pages/Market.jsx
import React, { useState, useEffect } from 'react';
import { 
  TrendingUp, 
  TrendingDown,
  BarChart3,
  MapPin,
  Calendar,
  Bell,
  Filter,
  Download,
  RefreshCw,
  Search,
  AlertCircle,
  Clock,
  DollarSign,
  Package,
  Target
} from 'lucide-react';
import api from '../services/api';

const Market = () => {
  const [marketData, setMarketData] = useState([]);
  const [trends, setTrends] = useState([]);
  const [predictions, setPredictions] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedCommodity, setSelectedCommodity] = useState('wheat');
  const [location, setLocation] = useState({ state: 'Maharashtra', district: 'Pune' });
  const [timeRange, setTimeRange] = useState('7d');
  const [priceAlerts, setPriceAlerts] = useState([]);

  const commodities = [
    { id: 'wheat', name: 'Wheat', unit: 'quintal' },
    { id: 'rice', name: 'Rice', unit: 'quintal' },
    { id: 'cotton', name: 'Cotton', unit: 'quintal' },
    { id: 'sugarcane', name: 'Sugarcane', unit: 'ton' },
    { id: 'maize', name: 'Maize', unit: 'quintal' },
    { id: 'soybean', name: 'Soybean', unit: 'quintal' },
    { id: 'onion', name: 'Onion', unit: 'quintal' },
    { id: 'potato', name: 'Potato', unit: 'quintal' }
  ];

  const states = [
    'Andhra Pradesh', 'Maharashtra', 'Punjab', 'Haryana', 'Uttar Pradesh',
    'Madhya Pradesh', 'Karnataka', 'Gujarat', 'Rajasthan', 'Tamil Nadu'
  ];

  useEffect(() => {
    fetchMarketData();
  }, [selectedCommodity, location, timeRange]);

  const fetchMarketData = async () => {
    try {
      setLoading(true);
      
      // Fetch current prices
      const pricesResponse = await api.getCurrentMarketPrices({
        commodity: selectedCommodity,
        state: location.state,
        district: location.district
      });
      
      // Fetch trends
      const trendsResponse = await api.getMarketTrends({
        commodity: selectedCommodity,
        duration: timeRange
      });
      
      // Fetch predictions
      const predictionsResponse = await api.getPricePredictions({
        commodity: selectedCommodity,
        state: location.state,
        days: 7
      });
      
      // Mock data for demonstration
      setMarketData([
        {
          market: 'Main Market - Pune',
          minPrice: 2050,
          maxPrice: 2180,
          modalPrice: 2120,
          date: new Date().toISOString(),
          volume: 450,
          arrivals: 320
        },
        {
          market: 'Wholesale Market - Mumbai',
          minPrice: 2080,
          maxPrice: 2200,
          modalPrice: 2150,
          date: new Date().toISOString(),
          volume: 680,
          arrivals: 520
        },
        {
          market: 'Agricultural Market - Nashik',
          minPrice: 2020,
          maxPrice: 2160,
          modalPrice: 2090,
          date: new Date().toISOString(),
          volume: 390,
          arrivals: 280
        }
      ]);
      
      setTrends(trendsResponse.data || []);
      setPredictions(predictionsResponse.data || []);
      
      // Mock price alerts
      setPriceAlerts([
        {
          id: 1,
          commodity: 'wheat',
          targetPrice: 2200,
          currentPrice: 2120,
          type: 'above',
          status: 'active',
          progress: 96.4
        },
        {
          id: 2,
          commodity: 'rice',
          targetPrice: 1800,
          currentPrice: 1950,
          type: 'below',
          status: 'triggered',
          progress: 108.3
        }
      ]);
      
    } catch (error) {
      console.error('Failed to fetch market data:', error);
    } finally {
      setLoading(false);
    }
  };

  const formatPrice = (price) => {
    return new Intl.NumberFormat('en-IN', {
      style: 'currency',
      currency: 'INR',
      minimumFractionDigits: 0
    }).format(price);
  };

  const getPriceChange = (current, previous) => {
    if (!previous) return { change: 0, percentage: 0 };
    const change = current - previous;
    const percentage = (change / previous) * 100;
    return { change, percentage };
  };

  const PriceCard = ({ market }) => {
    const priceChange = getPriceChange(market.modalPrice, market.modalPrice - 50); // Mock previous price
    
    return (
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
        <div className="flex items-start justify-between mb-4">
          <div>
            <h3 className="font-semibold text-gray-900">{market.market}</h3>
            <p className="text-sm text-gray-600 flex items-center mt-1">
              <MapPin size={12} className="mr-1" />
              {location.state}
            </p>
          </div>
          <div className="text-right">
            <p className="text-2xl font-bold text-gray-900">
              {formatPrice(market.modalPrice)}
            </p>
            <div className={`flex items-center space-x-1 text-sm ${
              priceChange.percentage >= 0 ? 'text-green-600' : 'text-red-600'
            }`}>
              {priceChange.percentage >= 0 ? 
                <TrendingUp size={14} /> : 
                <TrendingDown size={14} />
              }
              <span>{priceChange.percentage >= 0 ? '+' : ''}{priceChange.percentage.toFixed(1)}%</span>
            </div>
          </div>
        </div>
        
        <div className="grid grid-cols-2 gap-4 text-sm">
          <div>
            <p className="text-gray-600">Min Price</p>
            <p className="font-semibold">{formatPrice(market.minPrice)}</p>
          </div>
          <div>
            <p className="text-gray-600">Max Price</p>
            <p className="font-semibold">{formatPrice(market.maxPrice)}</p>
          </div>
          <div>
            <p className="text-gray-600">Volume</p>
            <p className="font-semibold">{market.volume} tons</p>
          </div>
          <div>
            <p className="text-gray-600">Arrivals</p>
            <p className="font-semibold">{market.arrivals} tons</p>
          </div>
        </div>
        
        <div className="mt-4 pt-4 border-t border-gray-200">
          <div className="flex items-center justify-between text-xs text-gray-500">
            <span>Updated: {new Date(market.date).toLocaleTimeString()}</span>
            <button className="text-blue-600 hover:text-blue-800">View Details</button>
          </div>
        </div>
      </div>
    );
  };

  const TrendChart = () => {
    // Mock trend data for visualization
    const mockTrends = [
      { date: '2024-12-10', price: 2080 },
      { date: '2024-12-11', price: 2095 },
      { date: '2024-12-12', price: 2070 },
      { date: '2024-12-13', price: 2110 },
      { date: '2024-12-14', price: 2125 },
      { date: '2024-12-15', price: 2120 },
      { date: '2024-12-16', price: 2140 }
    ];

    return (
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
        <div className="flex items-center justify-between mb-6">
          <h3 className="text-lg font-semibold text-gray-900">Price Trends</h3>
          <div className="flex items-center space-x-2">
            <select 
              value={timeRange}
              onChange={(e) => setTimeRange(e.target.value)}
              className="text-sm border border-gray-300 rounded px-2 py-1"
            >
              <option value="7d">7 Days</option>
              <option value="30d">30 Days</option>
              <option value="90d">90 Days</option>
            </select>
          </div>
        </div>
        
        {/* Simple trend visualization */}
        <div className="h-64 flex items-end space-x-2">
          {mockTrends.map((point, index) => {
            const height = ((point.price - 2000) / 200) * 100; // Normalize height
            return (
              <div key={index} className="flex-1 flex flex-col items-center">
                <div 
                  className="w-full bg-blue-500 rounded-t"
                  style={{ height: `${height}%` }}
                ></div>
                <div className="text-xs text-gray-500 mt-2 transform -rotate-45">
                  {new Date(point.date).toLocaleDateString('en-US', { month: 'short', day: 'numeric' })}
                </div>
              </div>
            );
          })}
        </div>
        
        <div className="mt-4 pt-4 border-t border-gray-200">
          <div className="grid grid-cols-3 gap-4 text-sm">
            <div className="text-center">
              <p className="text-gray-600">Avg Price</p>
              <p className="font-semibold">{formatPrice(2105)}</p>
            </div>
            <div className="text-center">
              <p className="text-gray-600">Highest</p>
              <p className="font-semibold text-green-600">{formatPrice(2140)}</p>
            </div>
            <div className="text-center">
              <p className="text-gray-600">Lowest</p>
              <p className="font-semibold text-red-600">{formatPrice(2070)}</p>
            </div>
          </div>
        </div>
      </div>
    );
  };

  if (loading) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-8"></div>
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            <div className="lg:col-span-2 space-y-6">
              <div className="h-64 bg-gray-200 rounded-lg"></div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="h-48 bg-gray-200 rounded-lg"></div>
                <div className="h-48 bg-gray-200 rounded-lg"></div>
              </div>
            </div>
            <div className="space-y-6">
              <div className="h-32 bg-gray-200 rounded-lg"></div>
              <div className="h-48 bg-gray-200 rounded-lg"></div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Header */}
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Market Intelligence</h1>
          <p className="text-gray-600 mt-2">
            Real-time commodity prices and market trends
          </p>
        </div>
        <div className="flex items-center space-x-3">
          <button
            onClick={fetchMarketData}
            className="flex items-center space-x-2 px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
          >
            <RefreshCw size={16} />
            <span>Refresh</span>
          </button>
          <button className="flex items-center space-x-2 px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors">
            <Bell size={16} />
            <span>Set Alert</span>
          </button>
        </div>
      </div>

      {/* Filters */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6 mb-8">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Commodity</label>
            <select
              value={selectedCommodity}
              onChange={(e) => setSelectedCommodity(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            >
              {commodities.map(commodity => (
                <option key={commodity.id} value={commodity.id}>
                  {commodity.name}
                </option>
              ))}
            </select>
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">State</label>
            <select
              value={location.state}
              onChange={(e) => setLocation(prev => ({ ...prev, state: e.target.value }))}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            >
              {states.map(state => (
                <option key={state} value={state}>
                  {state}
                </option>
              ))}
            </select>
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">District</label>
            <input
              type="text"
              value={location.district}
              onChange={(e) => setLocation(prev => ({ ...prev, district: e.target.value }))}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
              placeholder="Enter district"
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Time Range</label>
            <select
              value={timeRange}
              onChange={(e) => setTimeRange(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            >
              <option value="7d">Last 7 days</option>
              <option value="30d">Last 30 days</option>
              <option value="90d">Last 3 months</option>
            </select>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
        {/* Main Content */}
        <div className="lg:col-span-3 space-y-8">
          {/* Price Trend Chart */}
          <TrendChart />
          
          {/* Current Market Prices */}
          <div>
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-xl font-semibold text-gray-900">Current Prices</h2>
              <div className="flex items-center space-x-2 text-sm text-gray-600">
                <Clock size={14} />
                <span>Updated 5 minutes ago</span>
              </div>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6">
              {marketData.map((market, index) => (
                <PriceCard key={index} market={market} />
              ))}
            </div>
          </div>

          {/* Price Predictions */}
          <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
            <h2 className="text-xl font-semibold text-gray-900 mb-6">7-Day Price Forecast</h2>
            
            <div className="space-y-4">
              {[1, 2, 3, 4, 5, 6, 7].map(day => {
                const date = new Date();
                date.setDate(date.getDate() + day);
                const predictedPrice = 2120 + (Math.random() - 0.5) * 100;
                const confidence = 85 + Math.random() * 10;
                
                return (
                  <div key={day} className="flex items-center justify-between py-3 border-b border-gray-100 last:border-b-0">
                    <div>
                      <p className="font-medium text-gray-900">
                        {date.toLocaleDateString('en-US', { weekday: 'long', month: 'short', day: 'numeric' })}
                      </p>
                      <p className="text-sm text-gray-600">Confidence: {confidence.toFixed(1)}%</p>
                    </div>
                    <div className="text-right">
                      <p className="text-lg font-semibold text-gray-900">
                        {formatPrice(predictedPrice)}
                      </p>
                      <p className={`text-sm ${
                        predictedPrice > 2120 ? 'text-green-600' : 'text-red-600'
                      }`}>
                        {predictedPrice > 2120 ? '+' : ''}{((predictedPrice - 2120) / 2120 * 100).toFixed(1)}%
                      </p>
                    </div>
                  </div>
                );
              })}
            </div>
            
            <div className="mt-6 p-4 bg-blue-50 rounded-lg">
              <div className="flex items-start space-x-2">
                <AlertCircle className="text-blue-600 mt-0.5 flex-shrink-0" size={16} />
                <div>
                  <h4 className="font-medium text-blue-800">Market Analysis</h4>
                  <p className="text-blue-700 text-sm mt-1">
                    Prices are expected to remain stable with slight upward trend due to reduced supply and steady demand. 
                    Consider selling if prices exceed 2,200 per quintal.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Sidebar */}
        <div className="space-y-6">
          {/* Price Alerts */}
          <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
            <div className="flex items-center justify-between mb-4">
              <h3 className="font-semibold text-gray-900">Price Alerts</h3>
              <button className="text-sm text-green-600 hover:text-green-800">
                Add Alert
              </button>
            </div>
            
            <div className="space-y-3">
              {priceAlerts.map(alert => (
                <div key={alert.id} className="p-3 border border-gray-200 rounded-lg">
                  <div className="flex items-center justify-between mb-2">
                    <span className="font-medium text-gray-900 capitalize">
                      {alert.commodity}
                    </span>
                    <span className={`text-xs px-2 py-1 rounded-full ${
                      alert.status === 'active' ? 'bg-blue-100 text-blue-800' : 
                      'bg-green-100 text-green-800'
                    }`}>
                      {alert.status}
                    </span>
                  </div>
                  <div className="text-sm text-gray-600">
                    <p>{alert.type === 'above' ? 'Alert when above' : 'Alert when below'} {formatPrice(alert.targetPrice)}</p>
                    <p>Current: {formatPrice(alert.currentPrice)}</p>
                  </div>
                  <div className="mt-2">
                    <div className="flex justify-between text-xs text-gray-500 mb-1">
                      <span>Progress</span>
                      <span>{alert.progress.toFixed(1)}%</span>
                    </div>
                    <div className="w-full bg-gray-200 rounded-full h-1.5">
                      <div 
                        className={`h-1.5 rounded-full ${
                          alert.status === 'triggered' ? 'bg-green-500' : 'bg-blue-500'
                        }`}
                        style={{ width: `${Math.min(100, alert.progress)}%` }}
                      ></div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Market Statistics */}
          <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
            <h3 className="font-semibold text-gray-900 mb-4">Market Statistics</h3>
            
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <span className="text-gray-600">Daily Volume</span>
                <span className="font-semibold">1,520 tons</span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-gray-600">Weekly Average</span>
                <span className="font-semibold">{formatPrice(2105)}</span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-gray-600">Monthly High</span>
                <span className="font-semibold text-green-600">{formatPrice(2250)}</span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-gray-600">Monthly Low</span>
                <span className="font-semibold text-red-600">{formatPrice(1980)}</span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-gray-600">Volatility</span>
                <span className="font-semibold">Medium (4.2%)</span>
              </div>
            </div>
          </div>

          {/* Quick Actions */}
          <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
            <h3 className="font-semibold text-gray-900 mb-4">Quick Actions</h3>
            
            <div className="space-y-3">
              <button className="w-full flex items-center space-x-3 p-3 text-left bg-green-50 rounded-lg hover:bg-green-100 transition-colors">
                <Target className="text-green-600" size={20} />
                <div>
                  <p className="font-medium text-green-800">Set Price Target</p>
                  <p className="text-xs text-green-600">Get notified at optimal price</p>
                </div>
              </button>
              
              <button className="w-full flex items-center space-x-3 p-3 text-left bg-blue-50 rounded-lg hover:bg-blue-100 transition-colors">
                <BarChart3 className="text-blue-600" size={20} />
                <div>
                  <p className="font-medium text-blue-800">Market Analysis</p>
                  <p className="text-xs text-blue-600">Get detailed insights</p>
                </div>
              </button>
              
              <button className="w-full flex items-center space-x-3 p-3 text-left bg-purple-50 rounded-lg hover:bg-purple-100 transition-colors">
                <Download className="text-purple-600" size={20} />
                <div>
                  <p className="font-medium text-purple-800">Export Data</p>
                  <p className="text-xs text-purple-600">Download price history</p>
                </div>
              </button>
            </div>
          </div>

          {/* Related Commodities */}
          <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
            <h3 className="font-semibold text-gray-900 mb-4">Related Commodities</h3>
            
            <div className="space-y-3">
              {commodities.filter(c => c.id !== selectedCommodity).slice(0, 4).map(commodity => {
                const mockPrice = 1800 + Math.random() * 800;
                const mockChange = (Math.random() - 0.5) * 10;
                
                return (
                  <button
                    key={commodity.id}
                    onClick={() => setSelectedCommodity(commodity.id)}
                    className="w-full flex items-center justify-between p-3 text-left border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors"
                  >
                    <div>
                      <p className="font-medium text-gray-900">{commodity.name}</p>
                      <p className="text-sm text-gray-600">{formatPrice(mockPrice)}</p>
                    </div>
                    <div className={`flex items-center space-x-1 ${
                      mockChange >= 0 ? 'text-green-600' : 'text-red-600'
                    }`}>
                      {mockChange >= 0 ? <TrendingUp size={14} /> : <TrendingDown size={14} />}
                      <span className="text-sm">{mockChange >= 0 ? '+' : ''}{mockChange.toFixed(1)}%</span>
                    </div>
                  </button>
                );
              })}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Market;
