// frontend/src/pages/Weather.jsx
import React, { useState, useEffect } from 'react';
import { 
  Cloud, 
  Sun, 
  CloudRain, 
  Wind, 
  Eye, 
  Gauge,
  Thermometer,
  Droplets,
  Compass,
  Calendar,
  MapPin,
  RefreshCw,
  AlertTriangle,
  TrendingUp,
  TrendingDown,
  Navigation
} from 'lucide-react';
import api from '../services/api';
import geolocationService from '../services/geolocationService';

const Weather = () => {
  const [weatherData, setWeatherData] = useState(null);
  const [forecast, setForecast] = useState([]);
  const [alerts, setAlerts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [location, setLocation] = useState(null);
  const [locationInfo, setLocationInfo] = useState({ city: 'Loading...', country: '' });
  const [selectedDay, setSelectedDay] = useState(0);
  const [gettingLocation, setGettingLocation] = useState(false);

  useEffect(() => {
    initializeLocation();
  }, []);

  useEffect(() => {
    if (location) {
      fetchWeatherData();
    }
  }, [location]);

  const initializeLocation = async () => {
    try {
      setGettingLocation(true);
      console.log('Getting user location...');
      
      const userLocation = await geolocationService.getLocationWithCaching();
      console.log('User location obtained:', userLocation);
      
      setLocation({
        lat: userLocation.latitude,
        lon: userLocation.longitude
      });
      
      setLocationInfo({
        city: userLocation.city || 'Unknown',
        state: userLocation.state || '',
        country: userLocation.country || 'India'
      });
      
    } catch (error) {
      console.error('Failed to get location, using default:', error);
      // Fallback to default location (Delhi)
      setLocation({ lat: 28.6139, lon: 77.2090 });
      setLocationInfo({ city: 'New Delhi', state: 'Delhi', country: 'India' });
    } finally {
      setGettingLocation(false);
    }
  };

  const fetchWeatherData = async () => {
    try {
      setLoading(true);
      console.log('Fetching weather data for location:', location);
      
      // Fetch current weather with real coordinates
      const currentWeather = await api.getCurrentWeather(location.lat, location.lon);
      console.log('Current weather response:', currentWeather);
      setWeatherData(currentWeather.data);
      
      // Fetch forecast
      const forecastData = await api.getWeatherForecast(location.lat, location.lon, 7);
      console.log('Forecast response:', forecastData);
      setForecast(forecastData.data.forecasts || []);
      
      // Fetch weather alerts
      const alertsData = await api.getWeatherAlerts(location.lat, location.lon);
      console.log('Alerts response:', alertsData);
      setAlerts(alertsData.data || []);
      
    } catch (error) {
      console.error('Failed to fetch weather data:', error);
    } finally {
      setLoading(false);
    }
  };

  const refreshLocation = async () => {
    // Clear cache and get fresh location
    localStorage.removeItem('user_location');
    localStorage.removeItem('user_location_timestamp');
    await initializeLocation();
  };

  const getWeatherIcon = (weatherMain, size = 24) => {
    switch (weatherMain?.toLowerCase()) {
      case 'clear':
        return <Sun size={size} className="text-yellow-500" />;
      case 'clouds':
        return <Cloud size={size} className="text-gray-500" />;
      case 'rain':
      case 'drizzle':
        return <CloudRain size={size} className="text-blue-500" />;
      default:
        return <Cloud size={size} className="text-gray-500" />;
    }
  };

  const getTemperatureColor = (temp) => {
    if (temp >= 35) return 'text-red-600';
    if (temp >= 25) return 'text-orange-500';
    if (temp >= 15) return 'text-green-600';
    return 'text-blue-600';
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      weekday: 'long',
      month: 'long',
      day: 'numeric'
    });
  };

  const formatTime = (dateString) => {
    return new Date(dateString).toLocaleTimeString('en-US', {
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  if (loading) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-6"></div>
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            <div className="lg:col-span-2 space-y-6">
              <div className="h-64 bg-gray-200 rounded-lg"></div>
              <div className="h-48 bg-gray-200 rounded-lg"></div>
            </div>
            <div className="space-y-6">
              <div className="h-32 bg-gray-200 rounded-lg"></div>
              <div className="h-48 bg-gray-200 rounded-lg"></div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Header */}
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Weather Forecast</h1>
          <div className="flex items-center space-x-2 mt-2 text-gray-600">
            <MapPin size={16} />
            <span>{locationInfo.city}{locationInfo.state ? `, ${locationInfo.state}` : ''}, {locationInfo.country}</span>
            <span className="text-gray-400"></span>
            <span>Last updated: {weatherData ? formatTime(weatherData.timestamp) : '--'}</span>
            {gettingLocation && (
              <span className="text-blue-600 flex items-center space-x-1">
                <Navigation size={14} className="animate-spin" />
                <span>Getting location...</span>
              </span>
            )}
          </div>
        </div>
        <div className="flex items-center space-x-2">
          <button
            onClick={refreshLocation}
            className="flex items-center space-x-2 px-3 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors"
            disabled={gettingLocation}
          >
            <Navigation size={16} />
            <span>Update Location</span>
          </button>
          <button
            onClick={fetchWeatherData}
            className="flex items-center space-x-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            disabled={loading || !location}
          >
            <RefreshCw size={16} className={loading ? 'animate-spin' : ''} />
            <span>Refresh</span>
          </button>
        </div>
      </div>

      {/* Weather Alerts */}
      {alerts.length > 0 && (
        <div className="mb-8">
          <h2 className="text-xl font-semibold text-gray-900 mb-4">Weather Alerts</h2>
          <div className="space-y-3">
            {alerts.map((alert, index) => (
              <div 
                key={index}
                className={`p-4 rounded-lg border-l-4 ${
                  alert.severity === 'critical' ? 'bg-red-50 border-red-400' :
                  alert.severity === 'high' ? 'bg-orange-50 border-orange-400' :
                  'bg-yellow-50 border-yellow-400'
                }`}
              >
                <div className="flex items-start space-x-3">
                  <AlertTriangle 
                    className={
                      alert.severity === 'critical' ? 'text-red-600' :
                      alert.severity === 'high' ? 'text-orange-600' :
                      'text-yellow-600'
                    } 
                    size={20} 
                  />
                  <div>
                    <h3 className={`font-semibold ${
                      alert.severity === 'critical' ? 'text-red-800' :
                      alert.severity === 'high' ? 'text-orange-800' :
                      'text-yellow-800'
                    }`}>
                      {alert.type.replace('_', ' ').toUpperCase()}
                    </h3>
                    <p className={`text-sm mt-1 ${
                      alert.severity === 'critical' ? 'text-red-700' :
                      alert.severity === 'high' ? 'text-orange-700' :
                      'text-yellow-700'
                    }`}>
                      {alert.message}
                    </p>
                    <p className="text-xs text-gray-500 mt-2">
                      Valid until: {new Date(alert.endTime).toLocaleString()}
                    </p>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        {/* Main Weather Display */}
        <div className="lg:col-span-2 space-y-8">
          {/* Current Weather */}
          <div className="bg-gradient-to-br from-blue-50 to-indigo-100 rounded-lg shadow-lg p-8">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-semibold text-gray-900">Current Weather</h2>
              <div className="text-right text-sm text-gray-600">
                <p>{formatDate(new Date().toISOString())}</p>
                <p>{formatTime(new Date().toISOString())}</p>
              </div>
            </div>

            {weatherData && (
              <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                {/* Temperature Display */}
                <div className="flex items-center space-x-6">
                  <div className="flex-shrink-0">
                    {getWeatherIcon(weatherData.weather, 80)}
                  </div>
                  <div>
                    <div className="flex items-baseline space-x-2">
                      <span className={`text-6xl font-bold ${getTemperatureColor(weatherData.temperature)}`}>
                        {Math.round(weatherData.temperature)}
                      </span>
                      <span className="text-2xl text-gray-500">C</span>
                    </div>
                    <p className="text-xl text-gray-700 capitalize font-medium mt-2">
                      {weatherData.description}
                    </p>
                    <p className="text-gray-600 mt-1">
                      Feels like {Math.round(weatherData.temperature + 2)}C
                    </p>
                  </div>
                </div>

                {/* Weather Details */}
                <div className="grid grid-cols-2 gap-4">
                  <div className="bg-white/70 p-4 rounded-lg">
                    <div className="flex items-center space-x-2 mb-2">
                      <Droplets className="text-blue-500" size={20} />
                      <span className="text-sm font-medium text-gray-700">Humidity</span>
                    </div>
                    <p className="text-2xl font-bold text-gray-900">{weatherData.humidity}%</p>
                  </div>
                  
                  <div className="bg-white/70 p-4 rounded-lg">
                    <div className="flex items-center space-x-2 mb-2">
                      <Wind className="text-gray-600" size={20} />
                      <span className="text-sm font-medium text-gray-700">Wind</span>
                    </div>
                    <p className="text-2xl font-bold text-gray-900">{weatherData.windSpeed} km/h</p>
                  </div>
                  
                  <div className="bg-white/70 p-4 rounded-lg">
                    <div className="flex items-center space-x-2 mb-2">
                      <Eye className="text-purple-500" size={20} />
                      <span className="text-sm font-medium text-gray-700">Visibility</span>
                    </div>
                    <p className="text-2xl font-bold text-gray-900">
                      {Math.round(weatherData.visibility / 1000)} km
                    </p>
                  </div>
                  
                  <div className="bg-white/70 p-4 rounded-lg">
                    <div className="flex items-center space-x-2 mb-2">
                      <Gauge className="text-green-600" size={20} />
                      <span className="text-sm font-medium text-gray-700">Pressure</span>
                    </div>
                    <p className="text-2xl font-bold text-gray-900">{weatherData.pressure} hPa</p>
                  </div>
                </div>
              </div>
            )}
          </div>

          {/* 7-Day Forecast */}
          <div className="bg-white rounded-lg shadow-lg overflow-hidden">
            <div className="p-6 border-b border-gray-200">
              <h2 className="text-2xl font-semibold text-gray-900">7-Day Forecast</h2>
            </div>
            
            <div className="p-6">
              <div className="grid grid-cols-1 md:grid-cols-7 gap-4">
                {forecast.map((day, index) => (
                  <button
                    key={index}
                    onClick={() => setSelectedDay(index)}
                    className={`p-4 rounded-lg text-center transition-all ${
                      selectedDay === index
                        ? 'bg-blue-100 border-2 border-blue-500'
                        : 'bg-gray-50 hover:bg-gray-100 border-2 border-transparent'
                    }`}
                  >
                    <p className="text-sm font-medium text-gray-600 mb-2">
                      {index === 0 ? 'Today' : new Date(day.date).toLocaleDateString('en-US', { weekday: 'short' })}
                    </p>
                    <div className="flex justify-center mb-2">
                      {getWeatherIcon(day.weather, 24)}
                    </div>
                    <div className="space-y-1">
                      <p className="text-lg font-semibold text-gray-900">
                        {Math.round(day.tempMax)}
                      </p>
                      <p className="text-sm text-gray-500">
                        {Math.round(day.tempMin)}
                      </p>
                    </div>
                    {day.precipitation > 0 && (
                      <p className="text-xs text-blue-600 mt-2">
                        {Math.round(day.precipitation)}mm
                      </p>
                    )}
                  </button>
                ))}
              </div>

              {/* Selected Day Details */}
              {forecast[selectedDay] && (
                <div className="mt-6 p-4 bg-gray-50 rounded-lg">
                  <h3 className="font-semibold text-gray-900 mb-3">
                    {selectedDay === 0 ? 'Today' : formatDate(forecast[selectedDay].date)}
                  </h3>
                  <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                    <div>
                      <p className="text-gray-600">High/Low</p>
                      <p className="font-semibold">
                        {Math.round(forecast[selectedDay].tempMax)} / {Math.round(forecast[selectedDay].tempMin)}
                      </p>
                    </div>
                    <div>
                      <p className="text-gray-600">Humidity</p>
                      <p className="font-semibold">{Math.round(forecast[selectedDay].humidity)}%</p>
                    </div>
                    <div>
                      <p className="text-gray-600">Wind</p>
                      <p className="font-semibold">{Math.round(forecast[selectedDay].windSpeed)} km/h</p>
                    </div>
                    <div>
                      <p className="text-gray-600">Precipitation</p>
                      <p className="font-semibold">{Math.round(forecast[selectedDay].precipitation)}mm</p>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>

          {/* Farming Advisory */}
          <div className="bg-white rounded-lg shadow-lg p-6">
            <h2 className="text-2xl font-semibold text-gray-900 mb-4">Farming Weather Advisory</h2>
            
            <div className="space-y-4">
              <div className="flex items-start space-x-3 p-4 bg-green-50 rounded-lg">
                <div className="flex-shrink-0 w-2 h-2 bg-green-500 rounded-full mt-2"></div>
                <div>
                  <h4 className="font-medium text-green-800">Favorable for Field Operations</h4>
                  <p className="text-green-700 text-sm mt-1">
                    Current weather conditions are suitable for most farming activities including spraying and harvesting.
                  </p>
                </div>
              </div>

              <div className="flex items-start space-x-3 p-4 bg-yellow-50 rounded-lg">
                <div className="flex-shrink-0 w-2 h-2 bg-yellow-500 rounded-full mt-2"></div>
                <div>
                  <h4 className="font-medium text-yellow-800">Irrigation Recommendation</h4>
                  <p className="text-yellow-700 text-sm mt-1">
                    Consider light irrigation for sensitive crops due to low humidity levels. Best time: early morning or evening.
                  </p>
                </div>
              </div>

              <div className="flex items-start space-x-3 p-4 bg-blue-50 rounded-lg">
                <div className="flex-shrink-0 w-2 h-2 bg-blue-500 rounded-full mt-2"></div>
                <div>
                  <h4 className="font-medium text-blue-800">Weekly Outlook</h4>
                  <p className="text-blue-700 text-sm mt-1">
                    Rain expected mid-week. Plan harvest activities for early week and postpone spraying operations.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Sidebar */}
        <div className="space-y-6">
          {/* Weather Summary */}
          <div className="bg-white rounded-lg shadow-lg p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">Today's Summary</h3>
            
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <span className="text-gray-600">Sunrise</span>
                <span className="font-medium">6:45 AM</span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-gray-600">Sunset</span>
                <span className="font-medium">5:30 PM</span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-gray-600">UV Index</span>
                <span className="font-medium">6 (High)</span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-gray-600">Moon Phase</span>
                <span className="font-medium">Waxing Crescent</span>
              </div>
            </div>
          </div>

          {/* Air Quality */}
          <div className="bg-white rounded-lg shadow-lg p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">Air Quality</h3>
            
            <div className="text-center">
              <div className="w-20 h-20 mx-auto bg-green-100 rounded-full flex items-center justify-center mb-3">
                <span className="text-2xl font-bold text-green-600">45</span>
              </div>
              <p className="font-medium text-green-600">Good</p>
              <p className="text-sm text-gray-600 mt-1">Air quality is satisfactory</p>
            </div>

            <div className="mt-4 space-y-2 text-sm">
              <div className="flex justify-between">
                <span className="text-gray-600">PM2.5</span>
                <span>12 g/m</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">PM10</span>
                <span>25 g/m</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">O3</span>
                <span>68 g/m</span>
              </div>
            </div>
          </div>

          {/* Historical Comparison */}
          <div className="bg-white rounded-lg shadow-lg p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">Compare to Last Year</h3>
            
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <span className="text-gray-600">Temperature</span>
                <div className="flex items-center space-x-1">
                  <TrendingUp className="text-red-500" size={16} />
                  <span className="text-red-600 font-medium">+3C</span>
                </div>
              </div>
              
              <div className="flex items-center justify-between">
                <span className="text-gray-600">Rainfall</span>
                <div className="flex items-center space-x-1">
                  <TrendingDown className="text-blue-500" size={16} />
                  <span className="text-blue-600 font-medium">-15mm</span>
                </div>
              </div>
              
              <div className="flex items-center justify-between">
                <span className="text-gray-600">Humidity</span>
                <div className="flex items-center space-x-1">
                  <TrendingDown className="text-green-500" size={16} />
                  <span className="text-green-600 font-medium">-8%</span>
                </div>
              </div>
            </div>
          </div>

          {/* Quick Actions */}
          <div className="bg-white rounded-lg shadow-lg p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">Weather Actions</h3>
            
            <div className="space-y-3">
              <button className="w-full text-left p-3 bg-blue-50 rounded-lg hover:bg-blue-100 transition-colors">
                <div className="flex items-center space-x-3">
                  <Calendar className="text-blue-600" size={20} />
                  <div>
                    <p className="font-medium text-blue-800">Plan Field Activities</p>
                    <p className="text-xs text-blue-600">Based on 7-day forecast</p>
                  </div>
                </div>
              </button>
              
              <button className="w-full text-left p-3 bg-green-50 rounded-lg hover:bg-green-100 transition-colors">
                <div className="flex items-center space-x-3">
                  <Droplets className="text-green-600" size={20} />
                  <div>
                    <p className="font-medium text-green-800">Set Irrigation Alert</p>
                    <p className="text-xs text-green-600">Get notified when to water</p>
                  </div>
                </div>
              </button>
              
              <button className="w-full text-left p-3 bg-yellow-50 rounded-lg hover:bg-yellow-100 transition-colors">
                <div className="flex items-center space-x-3">
                  <AlertTriangle className="text-yellow-600" size={20} />
                  <div>
                    <p className="font-medium text-yellow-800">Weather Alerts</p>
                    <p className="text-xs text-yellow-600">Manage notification preferences</p>
                  </div>
                </div>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Weather;
