// src/hooks/useAuth.jsx
import React, { useState, useEffect, createContext, useContext } from 'react';

// Create Auth Context
const AuthContext = createContext();

// Auth Provider Component
export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  useEffect(() => {
    // Check for existing auth token and user data
    const initializeAuth = () => {
      try {
        const savedUser = localStorage.getItem('user');
        const savedToken = localStorage.getItem('authToken');
        
        if (savedUser && savedToken) {
          const userData = JSON.parse(savedUser);
          setUser(userData);
          setIsAuthenticated(true);
        } else {
          // For demo purposes, create a mock user
          const mockUser = {
            id: 'user_123',
            name: 'John Farmer',
            email: 'john.farmer@example.com',
            phone: '+1-555-0123',
            location: 'California, USA',
            farmSize: '50 acres',
            crops: ['Wheat', 'Corn', 'Tomatoes'],
            joinDate: '2023-01-15',
            avatar: null,
            subscription: 'Premium'
          };
          
          setUser(mockUser);
          setIsAuthenticated(true);
          localStorage.setItem('user', JSON.stringify(mockUser));
          localStorage.setItem('authToken', 'demo_token_' + Date.now());
        }
      } catch (error) {
        console.error('Error initializing auth:', error);
        // Fallback to mock user
        const mockUser = {
          id: 'user_123',
          name: 'John Farmer',
          email: 'john.farmer@example.com',
          phone: '+1-555-0123',
          location: 'California, USA',
          farmSize: '50 acres',
          crops: ['Wheat', 'Corn', 'Tomatoes'],
          joinDate: '2023-01-15',
          avatar: null,
          subscription: 'Premium'
        };
        
        setUser(mockUser);
        setIsAuthenticated(true);
      } finally {
        setLoading(false);
      }
    };

    initializeAuth();
  }, []);

  const login = async (credentials) => {
    try {
      setLoading(true);
      
      // Mock login - in real app, this would call your auth API
      const mockUser = {
        id: 'user_' + Date.now(),
        name: credentials.name || 'Farmer User',
        email: credentials.email,
        phone: credentials.phone || '+1-555-0123',
        location: credentials.location || 'Unknown',
        farmSize: credentials.farmSize || 'Not specified',
        crops: credentials.crops || ['Mixed crops'],
        joinDate: new Date().toISOString().split('T')[0],
        avatar: null,
        subscription: 'Premium'
      };

      const token = 'auth_token_' + Date.now();
      
      // Save to localStorage
      localStorage.setItem('user', JSON.stringify(mockUser));
      localStorage.setItem('authToken', token);
      
      setUser(mockUser);
      setIsAuthenticated(true);
      
      return { success: true, user: mockUser, token };
    } catch (error) {
      console.error('Login error:', error);
      return { success: false, error: error.message };
    } finally {
      setLoading(false);
    }
  };

  const logout = async () => {
    try {
      setLoading(true);
      
      // Clear localStorage
      localStorage.removeItem('user');
      localStorage.removeItem('authToken');
      
      setUser(null);
      setIsAuthenticated(false);
      
      return { success: true };
    } catch (error) {
      console.error('Logout error:', error);
      return { success: false, error: error.message };
    } finally {
      setLoading(false);
    }
  };

  const updateUser = async (userData) => {
    try {
      const updatedUser = { ...user, ...userData };
      
      localStorage.setItem('user', JSON.stringify(updatedUser));
      setUser(updatedUser);
      
      return { success: true, user: updatedUser };
    } catch (error) {
      console.error('Update user error:', error);
      return { success: false, error: error.message };
    }
  };

  const getAuthToken = () => {
    return localStorage.getItem('authToken');
  };

  const isTokenValid = () => {
    const token = getAuthToken();
    return !!token; // In real app, you'd validate the token
  };

  const value = {
    user,
    isAuthenticated,
    loading,
    login,
    logout,
    updateUser,
    getAuthToken,
    isTokenValid
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};

// Custom hook to use auth context
export const useAuth = () => {
  const context = useContext(AuthContext);
  
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  
  return context;
};

// Higher-order component for protected routes
export const withAuth = (Component) => {
  return function AuthenticatedComponent(props) {
    const { isAuthenticated, loading } = useAuth();
    
    if (loading) {
      return (
        <div className="auth-loading">
          <div className="spinner"></div>
          <p>Loading...</p>
        </div>
      );
    }
    
    if (!isAuthenticated) {
      return (
        <div className="auth-required">
          <p>Please log in to access this feature.</p>
        </div>
      );
    }
    
    return <Component {...props} />;
  };
};

export default useAuth;