// src/pages/RegisterPage.jsx
import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import {
  Mail,
  Lock,
  Eye,
  EyeOff,
  User,
  Phone,
  MapPin,
  Leaf,
  Users,
  Calendar,
  UserPlus,
  Loader,
  AlertCircle,
  CheckCircle,
  ArrowLeft,
  Sparkles
} from 'lucide-react';
import authService from '../services/authService';
import { useAuth } from '../hooks/useAuth.jsx';
import './Auth.css';

const RegisterPage = () => {
  const [currentStep, setCurrentStep] = useState(1);
  const [formData, setFormData] = useState({
    // Step 1: Basic Information
    name: '',
    email: '',
    password: '',
    confirmPassword: '',
    phone: '',
    
    // Step 2: Farm Information
    location: '',
    farmSize: '',
    farmSizeUnit: 'acres',
    farmType: '',
    
    // Step 3: Farming Experience
    experience: '',
    crops: [],
    irrigationType: '',
    challenges: []
  });
  
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  const navigate = useNavigate();
  const { login, isAuthenticated } = useAuth();

  // Redirect if already authenticated
  useEffect(() => {
    if (isAuthenticated) {
      navigate('/dashboard', { replace: true });
    }
  }, [isAuthenticated, navigate]);

  // Form options
  const farmTypes = [
    'Crop Farming',
    'Livestock Farming',
    'Mixed Farming',
    'Organic Farming',
    'Hydroponic Farming',
    'Greenhouse Farming'
  ];

  const experienceLevels = [
    'Beginner (0-2 years)',
    'Intermediate (3-5 years)',
    'Experienced (6-10 years)',
    'Expert (10+ years)'
  ];

  const commonCrops = [
    'Rice', 'Wheat', 'Corn', 'Barley', 'Oats',
    'Tomatoes', 'Potatoes', 'Onions', 'Carrots', 'Beans',
    'Cotton', 'Sugarcane', 'Soybeans', 'Sunflower',
    'Fruits', 'Vegetables', 'Herbs', 'Others'
  ];

  const irrigationTypes = [
    'Drip Irrigation',
    'Sprinkler Irrigation',
    'Flood Irrigation',
    'Manual Watering',
    'Rainwater Dependent',
    'Mixed Methods'
  ];

  const farmingChallenges = [
    'Weather Uncertainty',
    'Pest Management',
    'Soil Health',
    'Water Scarcity',
    'Market Access',
    'Labor Shortage',
    'Equipment Maintenance',
    'Crop Disease'
  ];

  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target;
    
    if (type === 'checkbox') {
      if (name === 'crops') {
        setFormData(prev => ({
          ...prev,
          crops: checked 
            ? [...prev.crops, value]
            : prev.crops.filter(crop => crop !== value)
        }));
      } else if (name === 'challenges') {
        setFormData(prev => ({
          ...prev,
          challenges: checked 
            ? [...prev.challenges, value]
            : prev.challenges.filter(challenge => challenge !== value)
        }));
      }
    } else {
      setFormData(prev => ({
        ...prev,
        [name]: value
      }));
    }
    
    // Clear errors when user starts typing
    if (error) setError('');
  };

  const validateStep = (step) => {
    switch (step) {
      case 1:
        if (!formData.name.trim()) {
          setError('Full name is required');
          return false;
        }
        if (!formData.email) {
          setError('Email is required');
          return false;
        }
        if (!/\S+@\S+\.\S+/.test(formData.email)) {
          setError('Please enter a valid email address');
          return false;
        }
        if (!formData.password) {
          setError('Password is required');
          return false;
        }
        if (formData.password.length < 8) {
          setError('Password must be at least 8 characters long');
          return false;
        }
        if (formData.password !== formData.confirmPassword) {
          setError('Passwords do not match');
          return false;
        }
        if (!formData.phone) {
          setError('Phone number is required');
          return false;
        }
        return true;

      case 2:
        if (!formData.location.trim()) {
          setError('Location is required');
          return false;
        }
        if (!formData.farmSize) {
          setError('Farm size is required');
          return false;
        }
        if (!formData.farmType) {
          setError('Please select your farm type');
          return false;
        }
        return true;

      case 3:
        if (!formData.experience) {
          setError('Please select your farming experience level');
          return false;
        }
        if (formData.crops.length === 0) {
          setError('Please select at least one crop type');
          return false;
        }
        if (!formData.irrigationType) {
          setError('Please select your irrigation method');
          return false;
        }
        return true;

      default:
        return true;
    }
  };

  const handleNext = () => {
    if (validateStep(currentStep)) {
      setCurrentStep(prev => prev + 1);
      setError('');
    }
  };

  const handleBack = () => {
    setCurrentStep(prev => prev - 1);
    setError('');
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');

    if (!validateStep(3)) return;

    setLoading(true);
    
    try {
      // Prepare user data for registration
      const userData = {
        name: formData.name,
        email: formData.email,
        password: formData.password,
        phone: formData.phone,
        location: formData.location,
        farmSize: `${formData.farmSize} ${formData.farmSizeUnit}`,
        farmType: formData.farmType,
        experience: formData.experience,
        crops: formData.crops,
        irrigationType: formData.irrigationType,
        challenges: formData.challenges
      };

      // Use the authService for real backend registration
      const result = await authService.register(userData);

      if (result.success) {
        setSuccess(result.message || 'Registration successful!');
        
        // Update auth context if auto-login occurred
        if (result.user && login) {
          await login(result.user, result.token);
        }
        
        // Redirect to dashboard after successful registration
        setTimeout(() => {
          navigate('/dashboard', { replace: true });
        }, 2000);
        
      } else {
        setError(result.message || 'Registration failed. Please try again.');
      }
    } catch (err) {
      console.error('Registration error:', err);
      setError('An unexpected error occurred. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const renderStep1 = () => (
    <motion.div
      key="step1"
      initial={{ opacity: 0, x: 20 }}
      animate={{ opacity: 1, x: 0 }}
      exit={{ opacity: 0, x: -20 }}
    >
      <div className="form-step-header">
        <h3>Personal Information</h3>
        <p>Let's start with your basic details</p>
      </div>

      <div className="form-group">
        <label htmlFor="name" className="form-label">Full Name</label>
        <div className="form-input-wrapper">
          <User className="form-input-icon" />
          <input
            type="text"
            id="name"
            name="name"
            className="form-input"
            placeholder="Enter your full name"
            value={formData.name}
            onChange={handleInputChange}
            required
          />
        </div>
      </div>

      <div className="form-group">
        <label htmlFor="email" className="form-label">Email Address</label>
        <div className="form-input-wrapper">
          <Mail className="form-input-icon" />
          <input
            type="email"
            id="email"
            name="email"
            className="form-input"
            placeholder="Enter your email"
            value={formData.email}
            onChange={handleInputChange}
            required
          />
        </div>
      </div>

      <div className="form-group">
        <label htmlFor="phone" className="form-label">Phone Number</label>
        <div className="form-input-wrapper">
          <Phone className="form-input-icon" />
          <input
            type="tel"
            id="phone"
            name="phone"
            className="form-input"
            placeholder="Enter your phone number"
            value={formData.phone}
            onChange={handleInputChange}
            required
          />
        </div>
      </div>

      <div className="form-group">
        <label htmlFor="password" className="form-label">Password</label>
        <div className="form-input-wrapper">
          <Lock className="form-input-icon" />
          <input
            type={showPassword ? 'text' : 'password'}
            id="password"
            name="password"
            className="form-input"
            placeholder="Create a strong password"
            value={formData.password}
            onChange={handleInputChange}
            required
          />
          <button
            type="button"
            className="form-input-toggle"
            onClick={() => setShowPassword(!showPassword)}
          >
            {showPassword ? <EyeOff /> : <Eye />}
          </button>
        </div>
      </div>

      <div className="form-group">
        <label htmlFor="confirmPassword" className="form-label">Confirm Password</label>
        <div className="form-input-wrapper">
          <Lock className="form-input-icon" />
          <input
            type={showConfirmPassword ? 'text' : 'password'}
            id="confirmPassword"
            name="confirmPassword"
            className="form-input"
            placeholder="Confirm your password"
            value={formData.confirmPassword}
            onChange={handleInputChange}
            required
          />
          <button
            type="button"
            className="form-input-toggle"
            onClick={() => setShowConfirmPassword(!showConfirmPassword)}
          >
            {showConfirmPassword ? <EyeOff /> : <Eye />}
          </button>
        </div>
      </div>
    </motion.div>
  );

  const renderStep2 = () => (
    <motion.div
      key="step2"
      initial={{ opacity: 0, x: 20 }}
      animate={{ opacity: 1, x: 0 }}
      exit={{ opacity: 0, x: -20 }}
    >
      <div className="form-step-header">
        <h3>Farm Information</h3>
        <p>Tell us about your farming operation</p>
      </div>

      <div className="form-group">
        <label htmlFor="location" className="form-label">Farm Location</label>
        <div className="form-input-wrapper">
          <MapPin className="form-input-icon" />
          <input
            type="text"
            id="location"
            name="location"
            className="form-input"
            placeholder="City, State/Region, Country"
            value={formData.location}
            onChange={handleInputChange}
            required
          />
        </div>
      </div>

      <div className="form-row">
        <div className="form-group">
          <label htmlFor="farmSize" className="form-label">Farm Size</label>
          <div className="form-input-wrapper">
            <Leaf className="form-input-icon" />
            <input
              type="number"
              id="farmSize"
              name="farmSize"
              className="form-input"
              placeholder="Enter size"
              value={formData.farmSize}
              onChange={handleInputChange}
              required
            />
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="farmSizeUnit" className="form-label">Unit</label>
          <select
            id="farmSizeUnit"
            name="farmSizeUnit"
            className="form-select"
            value={formData.farmSizeUnit}
            onChange={handleInputChange}
          >
            <option value="acres">Acres</option>
            <option value="hectares">Hectares</option>
            <option value="square_feet">Square Feet</option>
            <option value="square_meters">Square Meters</option>
          </select>
        </div>
      </div>

      <div className="form-group">
        <label htmlFor="farmType" className="form-label">Farm Type</label>
        <select
          id="farmType"
          name="farmType"
          className="form-select"
          value={formData.farmType}
          onChange={handleInputChange}
          required
        >
          <option value="">Select farm type</option>
          {farmTypes.map(type => (
            <option key={type} value={type}>{type}</option>
          ))}
        </select>
      </div>
    </motion.div>
  );

  const renderStep3 = () => (
    <motion.div
      key="step3"
      initial={{ opacity: 0, x: 20 }}
      animate={{ opacity: 1, x: 0 }}
      exit={{ opacity: 0, x: -20 }}
    >
      <div className="form-step-header">
        <h3>Farming Experience</h3>
        <p>Help us personalize your Farmora experience</p>
      </div>

      <div className="form-group">
        <label htmlFor="experience" className="form-label">Experience Level</label>
        <select
          id="experience"
          name="experience"
          className="form-select"
          value={formData.experience}
          onChange={handleInputChange}
          required
        >
          <option value="">Select experience level</option>
          {experienceLevels.map(level => (
            <option key={level} value={level}>{level}</option>
          ))}
        </select>
      </div>

      <div className="form-group">
        <label className="form-label">Crops You Grow (Select all that apply)</label>
        <div className="checkbox-grid">
          {commonCrops.map(crop => (
            <label key={crop} className="checkbox-label inline">
              <input
                type="checkbox"
                name="crops"
                value={crop}
                checked={formData.crops.includes(crop)}
                onChange={handleInputChange}
              />
              <span className="checkmark"></span>
              {crop}
            </label>
          ))}
        </div>
      </div>

      <div className="form-group">
        <label htmlFor="irrigationType" className="form-label">Primary Irrigation Method</label>
        <select
          id="irrigationType"
          name="irrigationType"
          className="form-select"
          value={formData.irrigationType}
          onChange={handleInputChange}
          required
        >
          <option value="">Select irrigation type</option>
          {irrigationTypes.map(type => (
            <option key={type} value={type}>{type}</option>
          ))}
        </select>
      </div>

      <div className="form-group">
        <label className="form-label">Main Farming Challenges (Optional)</label>
        <div className="checkbox-grid">
          {farmingChallenges.map(challenge => (
            <label key={challenge} className="checkbox-label inline">
              <input
                type="checkbox"
                name="challenges"
                value={challenge}
                checked={formData.challenges.includes(challenge)}
                onChange={handleInputChange}
              />
              <span className="checkmark"></span>
              {challenge}
            </label>
          ))}
        </div>
      </div>
    </motion.div>
  );

  return (
    <div className="auth-container">
      <div className="auth-background">
        <div className="auth-background-image"></div>
        <div className="auth-background-overlay"></div>
      </div>

      <div className="auth-content">
        {/* Left Side - Branding */}
        <motion.div 
          className="auth-branding"
          initial={{ opacity: 0, x: -50 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.6 }}
        >
          <div className="brand-logo">
            <Leaf className="brand-icon" />
            <Sparkles className="brand-sparkle" />
          </div>
          <h1 className="brand-title">Join Farmora</h1>
          <p className="brand-subtitle">
            Start your smart farming journey with AI-powered insights
          </p>
          
          {/* Progress Indicator */}
          <div className="registration-progress">
            <div className="progress-steps">
              {[1, 2, 3].map(step => (
                <div
                  key={step}
                  className={`progress-step ${step <= currentStep ? 'active' : ''} ${step < currentStep ? 'completed' : ''}`}
                >
                  <div className="step-number">{step}</div>
                  <div className="step-label">
                    {step === 1 && 'Personal'}
                    {step === 2 && 'Farm Info'}
                    {step === 3 && 'Experience'}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </motion.div>

        {/* Right Side - Registration Form */}
        <motion.div 
          className="auth-form-container"
          initial={{ opacity: 0, x: 50 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.6, delay: 0.2 }}
        >
          <div className="auth-form-wrapper">
            <div className="auth-form-header">
              <h2>Create Your Account</h2>
              <p>Step {currentStep} of 3</p>
            </div>

            {/* Success Message */}
            {success && (
              <motion.div 
                className="alert alert-success"
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
              >
                <CheckCircle className="alert-icon" />
                {success}
              </motion.div>
            )}

            {/* Error Message */}
            {error && (
              <motion.div 
                className="alert alert-error"
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
              >
                <AlertCircle className="alert-icon" />
                {error}
              </motion.div>
            )}

            <form onSubmit={handleSubmit} className="auth-form">
              {/* Render current step */}
              {currentStep === 1 && renderStep1()}
              {currentStep === 2 && renderStep2()}
              {currentStep === 3 && renderStep3()}

              {/* Navigation Buttons */}
              <div className="form-navigation">
                {currentStep > 1 && (
                  <button
                    type="button"
                    className="auth-back-btn"
                    onClick={handleBack}
                  >
                    <ArrowLeft className="btn-icon" />
                    Back
                  </button>
                )}

                {currentStep < 3 ? (
                  <button
                    type="button"
                    className="auth-next-btn"
                    onClick={handleNext}
                  >
                    Next
                    <ArrowLeft className="btn-icon next-icon" />
                  </button>
                ) : (
                  <button
                    type="submit"
                    className="auth-submit-btn"
                    disabled={loading}
                  >
                    {loading ? (
                      <>
                        <Loader className="btn-icon spinning" />
                        Creating Account...
                      </>
                    ) : (
                      <>
                        <UserPlus className="btn-icon" />
                        Create Account
                      </>
                    )}
                  </button>
                )}
              </div>
            </form>

            {/* Sign In Link */}
            <div className="auth-footer">
              <p>
                Already have an account?{' '}
                <Link to="/login" className="auth-link">
                  Sign in here
                </Link>
              </p>
            </div>

            {/* Back to Home */}
            <div className="auth-back">
              <Link to="/" className="back-btn">
                <ArrowLeft className="back-icon" />
                Back to Home
              </Link>
            </div>
          </div>
        </motion.div>
      </div>
    </div>
  );
};

export default RegisterPage;